	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Want to go for a slide?","date":"11 Aug 2005","slug":"/2005-08-11-want-to-go-for-a-slide/","tags":"Y2005"},"html":"<p>The <a href=\"http://www.airbus.com/en/\">Airbus</a> plant in Hamburg-Finkenwerder is looking for members of area sports clubs (for example <a href=\"http://www.blau-weiss-buchholz.de/schlagzeilen/airbus-testen.html\" title=\"Airbus A380 testen\">Blau-Weiss Buchholz</a>) to participate in an evacuation test of the <a href=\"http://www.airbus.com/en/aircraftfamilies/a380/\">A380</a> to be conducted in January. The test is to evacuate over 850 passengers the aircraft via the emergency slides within 90 seconds. Applicants must be between 18 and 60, be of normal weight and must pass a medical check. Those accepted will receive 60 Euro, food and a free tour of the plant. (via <a href=\"http://www.kreiszeitung-wochenblatt.de/\">Nordheide Wochenblatt</a>, our weekly shopper)</p>"},"next":{"fields":{"title":"Moving Blog","date":"10 Aug 2005","slug":"/2005-08-10-moving-blog/"}},"previous":{"fields":{"title":"Wir sind ein Volk (ausser Bayern)","date":"11 Aug 2005","slug":"/2005-08-11-wir-sind-ein-volk-ausser-bayern/"}}},{"node":{"fields":{"title":"Moving Blog","date":"10 Aug 2005","slug":"/2005-08-10-moving-blog/","tags":"Y2005"},"html":"<p>My boss will be moving to Cologne this fall, and since he sees weblogs as the solution to all of life's problems, he has started a <a href=\"http://umzug.lumma.de/\" title=\"Umzugsblog\">moving blog</a>.</p>\n<p>Moving is somewhat like sex... Americans do it better and more often. I've moved 12 times in my adult life, including cross-country and trans-continental, and 8 times since getting married (we can attest that moving is a good stress test for any relationship, although not nearly as good as building a house together). Moving is somewhat easier in the States since American apartments tend to include amenities like closets, floor covering, light fixtures and a kitchen, and are usually available (and can be vacated) at short notice. </p>\n<p>To answer a specific question: <a href=\"http://umzug.lumma.de/eintrag.php?id=20\" title=\"Praktische &Uuml;berlegungen\">how long can one live without a kitchen?</a> Answer: 10 weeks. Due to the physical constraints of the space-time continuum, when we moved to Germany we brought 4 suitcases and 3 cats with us on the plane and our furniture arrived 10 weeks later. We first moved temporarily into a unrenovated 6th-floor apartment in downtown Kiel, our first experience without the aforementioned amenities of closets, floor covering, light fixtures and a kitchen. We begged, borrowed, and bought from 2nd hand shops, and somehow survived until the container with our furniture arrived and found a permanent apartment in Hamburg. We'd never want to do it again, but if we had to we'd skip the shipping container and instead set up a credit line at Ikea.</p>\n<p>At the time we didn't yet have a child, which I imagine would increase the number of problems by at least a power of 2.</p>"},"next":{"fields":{"title":"Bad Tickets","date":"09 Aug 2005","slug":"/2005-08-09-bad-tickets/"}},"previous":{"fields":{"title":"Want to go for a slide?","date":"11 Aug 2005","slug":"/2005-08-11-want-to-go-for-a-slide/"}}},{"node":{"fields":{"title":"Bad Tickets","date":"09 Aug 2005","slug":"/2005-08-09-bad-tickets/","tags":"Y2005"},"html":"<p>My wife's company has been sponsoring the <a href=\"http://www.hsv.de/\">local soccer team</a> for I don't know how many years now, and today for the first time she brought home match tickets. But somehow I don't think the tickets are very good. </p>\n<p>For one thing, the match is against <a href=\"http://www.fcbayern.t-com.de/de/index.php\" title=\"FC Bayern M&uuml;nchen\">Bayern M&uuml;nchen</a>, a team they haven't beaten in years and a match they are sure to lose. </p>\n<p>And for another thing, the <a href=\"http://www.hsv.de/index.php?id=8817\" title=\"Hamburger SV: Business-Seats West\">seats are behind glass</a> and pretty far from the playing field. I'm sure we won't be able to <a href=\"http://www.hsv.de/index.php?id=12814\">see or hear well</a> at all.</p>\n<p>Well, the tickets didn't cost us anything, so I guess we should be happy that we'll be going to a game at all. At least they are for a league match and not for some <a href=\"http://fifaworldcup.yahoo.com/06/en/\">meaningless international tournament</a>.</p>"},"next":{"fields":{"title":"Half Empty or Half Dead?","date":"07 Aug 2005","slug":"/2005-08-07-half-empty-or-half-dead/"}},"previous":{"fields":{"title":"Moving Blog","date":"10 Aug 2005","slug":"/2005-08-10-moving-blog/"}}}],"pathPrefix":"","first":false,"last":false,"index":290,"pageCount":1101}}

